import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
	import news from '@/views/modules/news/list'
	import qichefenlei from '@/views/modules/qichefenlei/list'
	import yonghu from '@/views/modules/yonghu/list'
	import chat from '@/views/modules/chat/list'
	import discussxinnengyuanqiche from '@/views/modules/discussxinnengyuanqiche/list'
	import orders from '@/views/modules/orders/list'
	import discussremenqiche from '@/views/modules/discussremenqiche/list'
	import remenqiche from '@/views/modules/remenqiche/list'
	import config from '@/views/modules/config/list'
	import xinnengyuanqiche from '@/views/modules/xinnengyuanqiche/list'
	import newstype from '@/views/modules/newstype/list'


//2.   
export const routes = [{
	path: '/',
	name: 'System Homepage',
	component: Index,
	children: [{
		// main
		path: '/',
		name: 'System Homepage',
		component: Home,
		meta: {icon:'', title:'center', affix: true}
	}, {
		path: '/updatePassword',
		name: 'UPDATE',
		component: UpdatePassword,
		meta: {icon:'', title:'updatePassword'}
	}, {
		path: '/pay',
		name: '',
		component: pay,
		meta: {icon:'', title:'pay'}
	}, {
		path: '/center',
		name: '',
		component: center,
		meta: {icon:'', title:'center'}
	}
	,{
		path: '/news',
		name: '',
		component: news
	}
	,{
		path: '/qichefenlei',
		name: 'Car Categories',
		component: qichefenlei
	}
	,{
		path: '/yonghu',
		name: 'User',
		component: yonghu
	}
	,{
		path: '/chat',
		name: '',
		component: chat
	}
	,{
		path: '/discussxinnengyuanqiche',
		name: 'New Energy Vehicles',
		component: discussxinnengyuanqiche
	}
	,{
		path: '/orders/:status',
		name: 'Order Management',
		component: orders
	}
	,{
		path: '/discussremenqiche',
		name: 'Popular Cars',
		component: discussremenqiche
	}
	,{
		path: '/remenqiche',
		name: 'Popular Cars',
		component: remenqiche
	}
	,{
		path: '/config',
		name: '',
		component: config
	}
	,{
		path: '/xinnengyuanqiche',
		name: 'New Energy Vehicles',
		component: xinnengyuanqiche
	}
	,{
		path: '/newstype',
		name: '',
		component: newstype
	}
	]
	},
	{
		path: '/login',
		name: 'login',
		component: Login,
		meta: {icon:'', title:'login'}
	},
	{
		path: '/register',
		name: 'register',
		component: register,
		meta: {icon:'', title:'register'}
	},
	{
		path: '*',
		component: NotFound
	}
]
//3.VueRouter  
const router = new VueRouter({
	mode: 'hash',
	/*hashhistory*/
	routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//UPDATEpush
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}
export default router;
